import React from "react";
import getLanguage from "./MyLanguange";
import Popconfirms from "../../components/feedback/popconfirm";
import { Select } from "antd";
import IntlMessages from "../../components/utility/intlMessages";
const { Option } = Select;

const AddModule = (permission, redirect, lang, history, permissions) =>
  permissions.includes(permission) ? (
    <Option key={permission} value={undefined}>
      <Popconfirms
        title={<IntlMessages id="popconfirm.movePage" />}
        onConfirm={() => history.replace(redirect)}
        okText={<IntlMessages id="yes" />}
        cancelText={<IntlMessages id="no" />}
      >
        <div style={{ color: "#1890FF", fontStyle: "italic" }}>
          {" + " + getLanguage({ id: lang })}
        </div>
      </Popconfirms>
    </Option>
  ) : null;

export default AddModule;
