import React, { Component } from "react";
import { connect } from "react-redux";
import { compose } from "redux";
import CreditMemoCreateForm from "./createForm";
import notification from "../../../../components/notification";
import LayoutWrapper from "../../../../components/utility/layoutWrapper";
import PageHeader from "../../../../components/utility/pageHeader";
import IntlMessages from "../../../../components/utility/intlMessages";
import Button from "../../../../components/uielements/button";
import actions from "../../../../redux/financial/creditMemo/actions";
import cashBankActions from "../../../../redux/cashBank/cashBank/actions";
import { Box } from "../../../App/app.style";
import { Tabs } from "antd";

const TabPane = Tabs.TabPane;
const redirectTo = "/dashboard/financials/credit-memos";
const { storeCashBank } = cashBankActions;

class CreateCreditMemo extends Component {
  state = {
    visibleAddCashBank: false
  };
  // Get All data diawal
  componentDidMount() {
    const { createCreditMemo } = this.props;
    createCreditMemo();
  }

  componentDidUpdate() {
    // Display error notif
    if (this.props.creditMemo.error) {
      notification("error", this.props.creditMemo.error);
      this.props.clearError();
    }

    // Display success notif
    if (this.props.creditMemo.success) {
      this.props.history.replace(
        redirectTo + "/update/" + this.props.creditMemo.id_credit_memo
      );
    }

    // Cash Bank
    if (this.props.cashBank.error) {
      notification("error", this.props.cashBank.error);
      this.props.clearError();
    }

    if (this.props.cashBank.success) {
      const { createCreditMemo, clearSuccess } = this.props;
      notification("success", this.props.cashBank.success);
      this.handleModalAddCashBank();
      clearSuccess();
      createCreditMemo();
    }
  }

  handleModalAddCashBank = () => {
    this.setState({
      visibleAddCashBank: !this.state.visibleAddCashBank
    });
  };

  handleSubmit = value => {
    const { storeCreditMemo } = this.props;
    storeCreditMemo(value);
  };

  handleSubmitCashBank = value => {
    const { storeCashBank } = this.props;
    storeCashBank(value);
  };

  render() {
    const { user } = this.props.auth;
    let { permissions } = user ? user : [];
    permissions = permissions ? permissions : [];

    return (
      <LayoutWrapper>
        <Box>
          <PageHeader
            title={<IntlMessages id="creditMemos.add.title" />}
            style={{ margin: 0 }}
          >
            <Button onClick={() => this.props.history.replace(redirectTo)}>
              <IntlMessages id="button.back" />
            </Button>
          </PageHeader>

          <Tabs defaultActiveKey="1">
            <TabPane tab={<IntlMessages id="creditMemos.tab.detail" />} key="1">
              <CreditMemoCreateForm
                submit={this.handleSubmit}
                creditMemo={this.props.creditMemo}
                permissions={permissions}
                user={user}
                history={this.props.history}
                // Cash Bank
                cashBank={this.props.cashBank}
                visibleAddCashBank={this.state.visibleAddCashBank}
                handleModalAddCashBank={this.handleModalAddCashBank}
                handleSubmitCashBank={this.handleSubmitCashBank}
              />
            </TabPane>
            <TabPane
              tab={<IntlMessages id="creditMemos.tab.attachment" />}
              key="2"
              disabled
            ></TabPane>
          </Tabs>
        </Box>
      </LayoutWrapper>
    );
  }
}

const mapStateToProps = state => {
  return {
    auth: state.auth,
    creditMemo: state.creditMemo,
    cashBank: state.cashBank
  };
};

export default compose(connect(mapStateToProps, { ...actions, storeCashBank }))(
  CreateCreditMemo
);
