const actions = {
  getCreditMemos: (page, search, filter) => ({
    type: 'GET_CREDIT_MEMOS_REQUEST',
    payload: {
      page: page,
      search: search,
      filter: filter
    }
  }),
  createCreditMemo: () => ({
    type: 'CREATE_CREDIT_MEMO_REQUEST'
  }),
  storeCreditMemo: data => ({
    type: 'STORE_CREDIT_MEMO_REQUEST',
    payload: { body: data }
  }),
  editCreditMemo: id_credit_memo => ({
    type: 'EDIT_CREDIT_MEMO_REQUEST',
    payload: { id_credit_memo: id_credit_memo }
  }),
  updateCreditMemo: (id_credit_memo, data) => ({
    type: 'UPDATE_CREDIT_MEMO_REQUEST',
    payload: {
      id_credit_memo: id_credit_memo,
      body: data
    }
  }),
  showCreditMemo: id_credit_memo => ({
    type: 'SHOW_CREDIT_MEMO_REQUEST',
    payload: { id_credit_memo: id_credit_memo }
  }),
  processCreditMemo: id_credit_memo => ({
    type: "PROCESS_CREDIT_MEMO_REQUEST",
    payload: { id_credit_memo }
  }),
  cancelCreditMemo: (id_credit_memo, data) => ({
    type: "CANCEL_CREDIT_MEMO_REQUEST",
    payload: { 
      id_credit_memo,
      body: data
    }
  }),
  uploadDocument: (id_credit_memo, data) => ({
    type: "UPLOAD_CREDIT_MEMO_DOCUMENT_REQUEST",
    payload: {
      id_credit_memo,
      body : data
    }
  }),
  downloadDocument: id_credit_memo_document => ({
    type: "DOWNLOAD_CREDIT_MEMO_DOCUMENT_REQUEST",
    payload: {id_credit_memo_document}
  }),
  deleteDocument: id_credit_memo_document => ({
    type: "DELETE_CREDIT_MEMO_DOCUMENT_REQUEST",
    payload: { id_credit_memo_document }
  }),
  clearSuccess: () => ({
    type: "CLEAR_SUCCESS"
  }),
  clearError: () => ({
    type: "CLEAR_ERROR"
  }),
  clearErrorField: () => ({
    type: "CLEAR_ERROR_FIELD"
  })
};

export default actions;
