import React, { Component } from "react";
import moment from "moment";
import IntlMessages from "../../../../components/utility/intlMessages";
import getLanguage from "../../../App/MyLanguange";
import {
  Form,
  Input,
  Col,
  Modal,
  Select,
  InputNumber,
  Radio,
  DatePicker,
  Checkbox
} from "antd";
import { getCurrencyRate } from "../../../../helpers/helper";

const { Option } = Select;
const InputGroup = Input.Group;

class ModalAddCashBank extends Component {
  state = {
    is_cash: 1,
    bank_account_number: null,
    check_code: false
  };

  componentDidUpdate = () => {
    const { resetFields } = this.props.form;
    const { success } = this.props.cashBank;
    if (success) {
      resetFields();
    }
  };

  handleSubmit = e => {
    e.preventDefault();
    this.props.form.validateFieldsAndScroll((err, values) => {
      if (!err) {
        values.start_date = values.start_date.format("YYYY-MM-DD HH:mm:ss");
        this.props.submit(values);
      }
    });
  };

  handleChangeCode = () => {
    const { setFieldsValue } = this.props.form;
    this.setState({ check_code: !this.state.check_code });
    if (this.state.check_code === false) {
      setFieldsValue({
        ["cash_bank_code"]: null
      });
    }
  };

  handleChangeIsCash = e => {
    if (e.target.value === 0) {
      this.setState({
        is_cash: 0,
        bank_account_number: null
      });
    } else {
      this.setState({
        is_cash: 1,
        bank_account_number: null
      });
    }
  };

  handleChange = (name, e) => {
    //let name  = e.target.name
    let value = e.target.value;
    //console.log(e)
    this.setState({
      ...this.state.filter,
      [name]: value
    });
  };

  setFieldRate = (id_currency, date) => {
    const { currencies } = this.props.creditMemo;
    const { setFieldsValue } = this.props.form;

    currencies.map(data => {
      if (data.id_currency === id_currency) {
        let rate = getCurrencyRate(data.currency_rate, date);
        setFieldsValue({
          ob_rate: rate
        });
      }
    });
  };

  handleChangeStartDate = value => {
    const { getFieldValue } = this.props.form;
    if (getFieldValue("id_currency") && value instanceof moment) {
      this.setFieldRate(getFieldValue("id_currency"), value);
    }
  };

  handleChangeAccount = value => {
    const { setFieldsValue, getFieldValue } = this.props.form;
    const { accounts } = this.props.creditMemo;

    accounts.map(data => {
      if (data.id_account === value) {
        setFieldsValue({
          id_currency: data.id_currency
        });
        this.setFieldRate(data.id_currency, getFieldValue("start_date"));
      }
    });
  };

  render() {
    const { getFieldDecorator, getFieldError } = this.props.form;
    const { cashBank, creditMemo, id_location } = this.props;
    const loading = cashBank.loading;
    const { locations, accounts, currencies } = creditMemo;
    const error = cashBank.errorField ? cashBank.errorField : [];

    const formItemLayout = {
      labelCol: {
        xs: { span: 24 },
        sm: { span: 24 }
      },
      wrapperCol: {
        xs: { span: 24 },
        sm: { span: 24 }
      }
    };
    const formTextArea = {
      labelCol: {
        xs: { span: 24 },
        sm: { span: 24 }
      },
      wrapperCol: {
        xs: { span: 24 },
        sm: { span: 24 }
      }
    };

    return (
      <Modal
        title={<IntlMessages id="cashBanks.add.title" />}
        visible={this.props.visible}
        centered
        onCancel={this.props.handleModal}
        onOk={this.handleSubmit}
        width="40%"
        okText={getLanguage({ id: "button.submit" })}
        cancelText={getLanguage({ id: "button.cancel" })}
        okButtonProps={{ loading: loading }}
        cancelButtonProps={{ loading: loading }}
      >
        <Form layout="vertical" onSubmit={this.handleSubmit}>
          <div className="isoDetailDataFlex" style={{ padding: "0" }}>
            <InputGroup compact>
              <Col span={20}>
                <Form.Item
                  {...formItemLayout}
                  label={<IntlMessages id="cashBanks.add.code" />}
                  validateStatus={
                    error.cash_bank_code || getFieldError("cash_bank_code")
                      ? "error"
                      : null
                  }
                  help={
                    error.cash_bank_code
                      ? error.cash_bank_code
                      : getFieldError("cash_bank_code")
                  }
                >
                  {getFieldDecorator("cash_bank_code", {
                    //initialValue: data.cash_bank_code,
                    rules: [
                      {
                        max: 30,
                        message: <IntlMessages id="maxLength30" />
                      }
                    ]
                  })(<Input disabled={this.state.check_code ? true : false} />)}
                </Form.Item>
              </Col>
              <Col span={4}>
                <Form.Item
                  {...formItemLayout}
                  label={<IntlMessages id="autoGenerateCode" />}
                >
                  <Checkbox
                    style={{
                      paddingTop: "5px",
                      paddingLeft: "8px"
                    }}
                    checked={this.state.check_code}
                    onChange={this.handleChangeCode}
                  />
                </Form.Item>
              </Col>
            </InputGroup>

            <Form.Item
              {...formTextArea}
              label={<IntlMessages id="name" />}
              validateStatus={
                error.description || getFieldError("description")
                  ? "error"
                  : null
              }
              help={
                error.description
                  ? error.description
                  : getFieldError("description")
              }
            >
              {getFieldDecorator("description", {
                //initialValue: data.description,
                rules: [
                  {
                    required: true,
                    message: <IntlMessages id="required" />
                  },
                  {
                    max: 50,
                    message: <IntlMessages id="maxLength50" />
                  }
                ]
              })(<Input />)}
            </Form.Item>

            <Form.Item {...formItemLayout} label={<IntlMessages id="isCash" />}>
              {getFieldDecorator("is_cash", {
                initialValue: this.state.is_cash,
                rules: [
                  {
                    required: true,
                    message: <IntlMessages id="required" />
                  }
                ]
              })(
                <Radio.Group
                  style={{ display: "flex", width: "50%" }}
                  onChange={this.handleChangeIsCash.bind(this)}
                  buttonStyle="solid"
                >
                  <Radio.Button
                    style={{ flex: "1", textAlign: "center" }}
                    value={1}
                  >
                    <IntlMessages id="yes" />
                  </Radio.Button>
                  <Radio.Button
                    style={{ flex: "1", textAlign: "center" }}
                    value={0}
                  >
                    <IntlMessages id="no" />
                  </Radio.Button>
                </Radio.Group>
              )}
            </Form.Item>

            <Form.Item
              {...formItemLayout}
              label={<IntlMessages id="cashBanks.add.bankAccountNumber" />}
            >
              {getFieldDecorator("bank_account_number", {
                initialValue:
                  this.state.is_cash === 1
                    ? null
                    : this.state.bank_account_number,
                rules: [
                  {
                    max: 20,
                    message: <IntlMessages id="maxLength20" />
                  },
                  {
                    pattern: "^[0-9]*$",
                    message: <IntlMessages id="bankAccountMustNumber" />
                  }
                ]
              })(
                <Input
                  //addonBefore={is_cash}
                  onChange={this.handleChange.bind(this, "bank_account_number")}
                  disabled={this.state.is_cash === 1 ? true : false}
                  //style={{ textTransform: "uppercase" }}
                />
              )}
            </Form.Item>

            <Form.Item
              {...formItemLayout}
              label={<IntlMessages id="locations.pageHeader" />}
            >
              {getFieldDecorator("id_location", {
                initialValue: id_location,
                rules: [
                  {
                    required: true,
                    message: <IntlMessages id="required" />
                  }
                ]
              })(
                <Select
                  loading={cashBank.loading}
                  showSearch
                  optionFilterProp="children"
                  disabled={true}
                >
                  {locations.map((data, key) => (
                    <Option key={key} value={data.id_location}>
                      {data.location_code + " (" + data.location_name + ")"}
                    </Option>
                  ))}
                </Select>
              )}
            </Form.Item>

            <Form.Item
              {...formItemLayout}
              label={<IntlMessages id="accounts.pageHeader" />}
            >
              {getFieldDecorator("id_account", {
                //initialValue: data.id_account,
                rules: [
                  {
                    required: true,
                    message: <IntlMessages id="required" />
                  }
                ]
              })(
                <Select
                  loading={creditMemo.loading}
                  showSearch
                  optionFilterProp="children"
                  onChange={this.handleChangeAccount.bind(this)}
                >
                  {accounts.map((data, key) =>
                    data.id_account_type === "02" ? (
                      <Option key={key} value={data.id_account}>
                        {data.account_code + " (" + data.account_name + ")"}
                      </Option>
                    ) : null
                  )}
                </Select>
              )}
            </Form.Item>

            <InputGroup compact>
              <Col span={12}>
                <Form.Item
                  {...formItemLayout}
                  label={<IntlMessages id="currency" />}
                >
                  {getFieldDecorator("id_currency", {
                    //initialValue: 0,
                    rules: [
                      {
                        required: true,
                        message: <IntlMessages id="required" />
                      }
                    ]
                  })(
                    <Select
                      loading={creditMemo.loading}
                      showSearch
                      optionFilterProp="children"
                      style={{ width: "100%" }}
                      // onChange={this.handleChangeCurrency}
                      disabled={true}
                    >
                      {currencies.map((data, key) => (
                        <Option key={key} value={data.id_currency}>
                          {data.currency_code + " (" + data.currency_name + ")"}
                        </Option>
                      ))}
                    </Select>
                  )}
                </Form.Item>
              </Col>
              <Col span={12}>
                <Form.Item
                  {...formItemLayout}
                  label={<IntlMessages id="accounts.rate" />}
                >
                  {getFieldDecorator("ob_rate", {
                    rules: [
                      {
                        required: true,
                        message: <IntlMessages id="required" />
                      }
                    ]
                  })(
                    <InputNumber
                      formatter={value =>
                        `${value}`.replace(/\B(?=(\d{3})+(?!\d))/g, ",")
                      }
                      parser={value => value.replace(/\$\s?|(,*)/g, "")}
                      disabled={true}
                      style={{ width: "100%" }}
                      precision={2}
                    />
                  )}
                </Form.Item>
              </Col>
            </InputGroup>

            <InputGroup compact>
              <Col span={12}>
                <Form.Item
                  {...formItemLayout}
                  label={<IntlMessages id="accounts.openingBalance" />}
                >
                  {getFieldDecorator("opening_balance", {
                    initialValue: 0,
                    rules: [
                      {
                        required: true,
                        message: <IntlMessages id="required" />
                      }
                    ]
                  })(
                    <InputNumber
                      formatter={value =>
                        `${value}`.replace(/\B(?=(\d{3})+(?!\d))/g, ",")
                      }
                      parser={value => value.replace(/\$\s?|(,*)/g, "")}
                      min={0}
                      style={{ width: "100%" }}
                      precision={2}
                    />
                  )}
                </Form.Item>
              </Col>

              <Col span={12}>
                <Form.Item
                  {...formItemLayout}
                  label={<IntlMessages id="accounts.openingBalanceDate" />}
                >
                  {getFieldDecorator("start_date", {
                    initialValue: moment(new Date())
                  })(
                    <DatePicker
                      style={{ width: "100%" }}
                      allowClear={false}
                      showTime
                      format="DD MMMM YYYY HH:mm:ss"
                      disabledDate={e => e.valueOf() > moment()}
                      onChange={this.handleChangeStartDate}
                      onOpenChange={this.handleChangeStartDate}
                    />
                  )}
                </Form.Item>
              </Col>
            </InputGroup>
          </div>
        </Form>
      </Modal>
    );
  }
}

const WrappedModalAddCashBank = Form.create({
  name: "modal_add_cash_bank"
})(ModalAddCashBank);

export default WrappedModalAddCashBank;
